import React, { Component } from 'react';

import ContactInfo from './ContactInfo';

class Contact extends Component {
    state = {
        keyword: '',
        contactData: [{
            name: 'David',
            phone: '010-1234-5678'
        }, {
            name: 'Albert',
            phone: '010-1234-1234'
        }, {
            name: 'John',
            phone: '010-5678-5678'
        }, {
            name: 'Wade',
            phone: '010-4312-5678'
        },]
    }
    _searchContact = (e) => { // 2. input      this.state.keyword  
        this.setState({
            keyword: e.target.value
        });
    }
    render() {
        const mapToComponents = (data) => { // 3.  (  this.state.contactData)
            data.sort(); // 3-1. this.state.contactData   (  )
            data = data.filter( // 3-2.   
                (contact) => { //   element[, index, array]       
                    return contact.name.toLowerCase() // ,   ,  (indexOf )
                        .indexOf(this.state.keyword.toLowerCase()) > -1; // indexOf    (string)     -1
                }
            ); // 4. input        .    data 
            return data.map( // 5.  data  
                (contact, i) => { // map     - item,    - index
                    return (<ContactInfo contact={contact} key={i} />); // map          
                } // 6. ContactInfo    
            );
        }
        return (
            <div>
                <h1>Contact</h1>
                <input
                    name="keyword"
                    placeholder="Search"
                    value={this.state.keyword}
                    onChange={this._searchContact} // 1. input    
                />
                <div>{mapToComponents(this.state.contactData)}</div> {/*7.  */}
            </div>
        )
    }
}

export default Contact;